.card {
  position: relative;
  display: block;
  max-width: 530px;
  text-decoration: none;
  color: $dark-brown;
  transition: transform 0.2s ease;
  will-change: transform;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.2); }
  &:hover {
    transform: translateY(-5px); }
  @include r(767) {
    max-width: 450px;
    margin: 0 auto; }
  &--sale {
    &:before {
      display: none; }
    .card__description {
      display: block;
      height: auto;
      padding: 20px 0 0;
      span {
        display: inline;
        overflow: auto;
        max-width: initial; }
      p {
        &:not(:last-child) {
          margin-bottom: 12px; } } } }
  &__inner {
    position: relative; }
  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden; }
  &__wave {
    position: absolute;
    top: -50px;
    left: -65px; }
  &__title {
    position: relative;
    padding: 20px 92px 0 14px;
    color: #fff;
    @include r(767) {
      padding-right: 72px;
      padding-top: 15px; } }
  &__subtitle {
    font-size: 16px;
    line-height: 1.35; }
  &__description-title {
    margin-bottom: 12px; }
  &__pic {
    display: block;
    width: 100%;
    height: 330px;
    background-color: #c4c4c4;
    object-fit: cover;
    font-size: 'object-fit: cover';
    @include r(1024) {
      height: 280px; }
    @include r(767) {
      height: 250px; } }
  &__price {
    position: absolute;
    left: 0;
    bottom: 0;
    min-width: 134px;
    padding: 18px 20px;
    text-align: center;
    background: $yellow;
    @include proxima-b;
    font-size: rem(23);
    line-height: 1;
    @include r(1024) {
      min-width: 115px;
      padding: 13px 20px;
      font-size: rem(20); }
    @include r(767) {
      font-size: remMob(18); } }
  &__description {
    position: relative;
    display: flex;
    height: 60px;
    align-items: center;
    padding: 5px 14px;
    span {
      display: inline-block;
      max-height: rem(50);
      overflow: hidden; }
    @include r(767) {
      height: auto;
      padding: 7px 14px;
      span {
        max-height: initial; } } }
  &__gift {
    position: absolute;
    top: -34px;
    right: 9px;
    max-width: 50px;
    @include r(1024) {
      max-width: 40px;
      top: -27px;
      right: -10px; }
    @include r(767) {
      max-width: 35px;
      top: -25px;
      right: -7px; } }
  &__button {
    margin-top: 20px;
    .button {
      @include r(767) {
        display: block;
        max-width: 250px;
        text-align: center; } } } }

.calendar {
  position: absolute;
  top: 3px;
  right: 14px;
  width: 78px;
  padding: 16px 0;
  text-align: center;
  background: #fff;
  box-shadow: 0px 4px 5px rgba(48, 48, 47, 0.25);
  border-radius: 5px;
  font-size: 15px;
  @include r(1024) {
    width: 70px;
    padding: 12px 0; }
  @include r(767) {
    right: 5px;
    width: 63px;
    padding: 10px 0; }
  &__date {
    display: block;
    @include proxima-sb;
    font-size: rem(18);
    @include r(767) {
      font-size: remMob(15); } }
  &__stroke {
    position: absolute;
    top: -7px;
    left: 50%;
    width: 35px;
    height: 13px;
    margin-left: -17.5px;
    border-right: 1px solid $dark-brown;
    border-left: 1px solid $dark-brown;
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      width: 1px;
      height: 13px;
      margin-left: -0.5px;
      background: $dark-brown; } } }
