    margin: -10px 0 27px;
  }
}

.content .time {
  position: relative;
  margin-bottom: 25px;
  padding-left: 15px;
  font-family: 'proxima-nova-regular-italic', sans-serif;
}

.content .time:before {
  content: '';
  position: absolute;
  top: -8px;
  left: 0;
  width: 5px;
  height: 38px;
  background: #FDD80C;
}

@media only screen and (max-width: 767px) {
  .content .time:before {
    top: -5px;
    height: 30px;
  }
}

.content .time b {
  font-family: 'proxima-nova-semibold', sans-serif;
  color: #111110;
}

.content .notes p {
  margin-bottom: 0;
}

.content .notes b {
  font-family: 'proxima-nova-semibold', sans-serif;
  color: #FF001F;
}

.content .list-group {
  display: flex;
  margin: 0 -10px;
}

@media only screen and (max-width: 1024px) {
  .content .list-group {
    flex-wrap: wrap;
  }
}

.content .list-group div {
  margin: 0 10px;
  padding: 25px 10px 25px 15px;
  border: 3px solid #FDD80C;
}

.content .list-group div:first-child {
  flex-basis: 50%;
}

.content .list-group div:first-child ul li:before {
  background-image: url(../icons/checkmark-blue.svg);
}

.content .list-group div:last-child {
  flex-basis: 50%;
  margin-bottom: 0;
}

.content .list-group div:only-child {
  flex-basis: 100%;
}

@media only screen and (max-width: 1024px) {
  .content .list-group div {
    margin-bottom: 20px;
  }
  .content .list-group div:first-child, .content .list-group div:last-child {
    flex-basis: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .content .list-group div {
    padding: 20px 10px 20px 15px;
  }
}

.content .list-group p {
  margin-bottom: 16px;
}

.content .list-group b {
  font-family: 'proxima-nova-semibold', sans-serif;
}

.content .list-group ul li {
  position: relative;
  margin-bottom: 10px;
  padding-left: 29px;
}

.content .list-group ul li:before {
  content: '';
  position: absolute;
  top: 7px;
  left: 0;
  width: 17px;
  height: 11px;
  background-image: url(../icons/checkmark-pink.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.content .list-group ul li:last-child {
  margin-bottom: 0;
}

.content .button {
  display: block;
  max-width: 209px;
  margin: 40px auto 0;
  padding: 13px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .content .button {
    max-width: 320px;
  }
}

.content .button--mob {
  display: none;
}

@media only screen and (max-width: 767px) {
  .content .button--mob {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .content .button + h3 {
    margin-top: 30px;
  }
}

.contacts {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 15px;
}

.contacts__inner {
  display: flex;
  margin-top: 55px;
}

@media only screen and (max-width: 767px) {
  .contacts__inner {
    flex-wrap: wrap;
  }
}

.contacts__bl {
  flex-basis: 100%;
}

.contacts__bl:first-child {
  margin-right: 55px;
  max-width: 350px;
}

@media only screen and (max-width: 1024px) {
  .contacts__bl:first-child {
    margin-right: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .contacts__bl:first-child {
    max-width: 400px;
    margin: 0 auto 40px;
  }
}

.contacts__video {
  margin-top: 40px;
}

.contacts__video b {
  display: block;
  margin-bottom: 15px;
  font-family: 'proxima-nova-semibold', sans-serif;
}

.form--modal .form__row {
  margin-bottom: 5px;
}

.form--modal .form__button {
  margin-top: 15px;
}

.form__title {
  margin-bottom: 25px;
}

.form__row {
  margin-bottom: 20px;
}

.form__input, .form__textarea {
  width: 100%;
  padding: 12px 12px 11px;
  font-family: 'proxima-nova', sans-serif;
  font-size: 1.0625rem;
  border: 1px solid #bdbdbb;
}

.form__input.is-error, .form__textarea.is-error {
  border-color: #FF001F;
  margin-bottom: 5px;
}

.form__input.is-error + .form__helper, .form__textarea.is-error + .form__helper {
  display: block;
}

.form__textarea {
  height: 120px;
  resize: none;
}

.form__helper {
  display: none;
  font-family: 'proxima-nova-bold', sans-serif;
  font-size: 0.8125rem;
  color: #FF001F;
}

.address {
  display: flex;
  margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .address {
    max-width: 400px;
    margin: 0 auto;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.address__bl {
  flex-basis: 100%;
  padding-left: 15px;
}

@media only screen and (max-width: 767px) {
  .address__bl {
    max-width: 170px;
    padding: 0;
    margin-bottom: 20px;
    flex-basis: auto;
  }
}

.address__bl:first-child {
  max-width: 230px;
  padding-right: 15px;
  padding-left: 0;
}

.address__bl address {
  font-style: normal;
}

.address__bl b {
  display: block;
  margin-bottom: 5px;
  font-family: 'proxima-nova-semibold', sans-serif;
  color: #111110;
}

.phone-number__link {
  text-decoration: none;
  color: inherit;
}

.video iframe {
  width: 100%;
}
